<template>
  <div>
      <div class="box">
          <Head>
              <span slot="left">
                  ele.me
              </span>
              <router-link to="/Login" tag="p" slot="right" v-show="isShow">
                 登陆 |
              </router-link> 
              <router-link to="/Register" tag="p" slot="right" v-show="isShow">
                 注册
              </router-link>   
              <img src="../../images/120.png" alt="" slot="right" v-show="isHow">      
          </Head>
          <div class="content">
              <div class="city">
                 <span>当前定位城市：</span>
                 <p>定位不准确时，请在城市列表中选择</p>
                 <!-- <hr /> -->
              </div>
              <div class="bj">
                  <span>
                     {{lists}}
                  </span>
                 <p>></p>
              </div>
              <div class="bl">

              </div>
              <div class="re">
                 <span>热门城市</span>
              </div>
              <div class="remen">
                 <ul>
                   <li v-for="tem in lock">{{tem.name}}</li>
                 </ul>
              </div>
              <div class="quanbu" v-for="(item,index) in zimu" :key="index">
                <p>{{item}}<span>{按字母排序}</span></p>
                <ul>
                    <!-- <li v-for="(i,index) in all[item]" :key="index">
                      <router-link :to="{name:'Search',params:{id:i.id}}" :key="i.id">
                        {{i.name}}
                      </router-link>
                    </li> -->
                  <router-link tag="li" v-for="(tem,index) in all[item]" :key="tem.id" :to="{name:'Search',params:{id:tem.id}}">
                      {{tem.name}}
                  </router-link>
                </ul>
              </div>
          </div>
      </div>
  </div>
</template>

<script>
import Head from  '../Head/Header.vue'
import axios from 'axios'
export default {
  components:{
    Head
  },
  data(){
    return{
      lists:[],
      lock:[],
      all:[],
      zimu:[],
      isShow:true,
      isHow:false
    }
  },
  mounted(){
    function  rem(){
      document.documentElement.style.fontSize=document.documentElement.clientWidth/7.5+"px";
    }
    //onresize  当屏幕的尺寸发生改变的时候会触发的事件
    rem();
    window.onresize=rem;
    this.axios.get('http://elm.cangdu.org/v1/cities?type=guess').then(res=>{
      this.lists = res.data.name
    })
    this.axios.get('http://elm.cangdu.org/v1/cities?type=hot').then(res=>{
      this.lock=res.data
    })
    this.axios.get("http://elm.cangdu.org/v1/cities?type=group").then(res=>{
      this.all = res.data
      this.zimu = Object.keys(res.data).sort()
    })
  }
}
</script>

<style lang="less" scoped>
    .box{
      width: 100%;
      header{
        width: 100%;
        height: 0.8rem;
        background: #3792E5;
        position: relative;
        span{
          display: block;
          color: white;
          position: absolute;
          top: 0.2rem;
          left: 0.2rem;
          font-size: 0.3rem;
        }
        p{
          // width: 0.4rem;
          display: block;
          position: absolute;
          font-size: 0.3rem;
          top: -0.1rem;
          color: white;
          right: 0.8rem;
        }
        p:nth-of-type(2){
          display: block;
          color: white;
          font-size: 0.3rem;
          top: -0.1rem;
          right: 0.13rem;
        }
        img{
          position: absolute;
          top: 0.1rem;
          right: 0.2rem;
        }
      }
      .content{
        width: 100%;
        .city{
          width: 100%;
          height: 0.8rem;
          position: relative;
          border-bottom: 1px solid #E4E4E4;
          span{
             display: block;
             font-size: 0.3rem;
             position: absolute;
             top: 0.3rem;
             left: 0.1rem;
          }
          p{
            position: absolute;
            top: 0;
            right: 0.1rem;
            font-size: 0.3rem;
            display: block;
          }
        }
        .bj{
          width: 100%;
          height: 0.8rem;
          border-bottom: 1px solid #E4E4E4;
          position: relative;
          span{
            top: 0.2rem;
            left: 0.2rem;
            display: block;
            position: absolute;
            font-size: 0.4rem;
            color: #3792E5;
          }
          p{
            display: block;
            position: absolute;
            top: -0.2rem;
            right: 0.2rem;
            font-size: 0.4rem;
          }
        }
        .bl{
          width: 100%;
          height: 10px;
          background: #F5F5F5;
        }
        .re{
          width: 100%;
          height: 0.8rem;
          border-top: 1px solid #E4E4E4;
          border-bottom: 1px solid #E4E4E4;
          position: relative;
          span{
            display: block;
            position: absolute;
            top: 0.2rem;
            left: 0.2rem;
            font-size: 0.4rem;
          }
        }
        .remen{
          width: 100%;
          // margin-left: 5px;
          ul{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            list-style: none;
            // margin-left: -0.65rem;
            margin-top: 0.3rem;
            li{
              width: 24%;
              height: 0.6rem;
              border: 1px solid #E4E4E4;
              line-height: 0.6rem;
              text-align: center;
              color: #3792E5;
              font-size: 0.3rem;
            }
          }
        }
        .quanbu{
          // margin-top: -0.7rem;
          p{
            display: block;
            width: 100%;
            // margin-top: -0.7rem;
            margin-bottom: 0.5rem;
            font-size: 0.4rem;
          }
          width: 100%;
          margin-left: 0.3rem;
          margin-bottom: 0.1rem;
          ul{
            width: 100%;
            display: flex;
            flex-wrap: wrap;
            list-style: none;
            margin-left: -0.2rem;
            margin-top: 0rem;
            li{
              width: 24%;
              height: 0.6rem;
              border: 1px solid #E4E4E4;
              line-height: 0.6rem;
              text-align: center;
              font-size: 0.25rem;
            }
          }
        }
      }
    }
</style>


